<template>
    <div id="map"></div>
    <!-- <div class="button-group">
        <button @click="changeSize(0)">Hide</button>
        <button @click="changeSize(400)">show</button>
        <button @click="displayMarker(markerPositions1)">marker set 1</button>
        <button @click="displayMarker(markerPositions2)">marker set 2</button>
        <button @click="displayMarker([])">marker set 3 (empty)</button>
        <button @click="displayInfoWindow">infowindow</button>
    </div>         -->
    <!-- {{ this.height }}
    {{ this.width }} -->
    <div class="bot">
        <b-button pill variant="primary" @click="displayOleum"></b-button>
        <b-button pill variant="primary" @click="displayOlle"></b-button>
        <b-button pill variant="primary" @click="displayHalla"> </b-button>
        <b-button pill variant="primary" @click="displayHiking"></b-button>
    </div>

</template>

<script>
import { toRaw } from "vue";
import {ref} from 'vue';
var apikey= ref('');

export default {
    name: "KakaoMap",
    props:{
        height:String,
        width:String,
        oleum_data:Object,
        olle_data:Object,
        halla_data:Object,
        hiking_data:Object,
    },

    data() {
        return {            
        };
    },

    created() {
        // console.log(process.env.VUE_APP_KAKAOMAP_API_KEY);
        apikey = process.env.VUE_APP_KAKAOMAP_API_KEY;
        // console.log('api_key ', apikey);
    },

    mounted() {
        if (! window.kakao || !window.kakao.maps) {            
            const script = document.createElement("script");
            script.onload = () => kakao.maps.load(this.initMap);
            script.src ="//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey="+apikey;
            document.head.appendChild(script);            
        } else {
            this.initMap();            
        }
    },

    methods: {        
        initMap() {
            const container = document.getElementById("map");
            
            container.style.width = `${this.width}`;
            container.style.height = `${this.height}`;
            
            const options = {
                center: new kakao.maps.LatLng(33.38, 126.58),
                level: 10,
            };
            this.map = new kakao.maps.Map(container, options);            
            this.map.relayout();
            
            // console.log(this.map);
            // toRaw(this.map).relayout();
        },

        displayOleum() {

            let features = this.oleum_data.features;
            features.forEach( element => {
                // console.log(element);
                // console.log(element.geometry.coordinates, element.properties.);
                // let site = element.properties.;

                let position = new kakao.maps.LatLng(element.geometry.coordinates[1], element.geometry.coordinates[0]);
                let dia = element.properties['']; 
                let name = element.properties['']
                
                new kakao.maps.Circle({
                    map: toRaw(this.map),
                    center : position,
                    radius: dia,    
                    strokeColor: '#5347AA',
                    strokeOpacity: 0.5,
                    strokeWeight: 2,
                    fillColor: '#E51D1A',
                    fillOpacity: 0.3,
                    //    
                    // radius: 5000, //     
                    // strokeWeight: 5, //   
                    // strokeColor: '#75B8FA', //  
                    // strokeOpacity: 1, //    1 0   0  
                    // strokeStyle: 'dashed', //   
                    // fillColor: '#CFE7FF', //  
                    // fillOpacity: 0.7  //      
                });

                new kakao.maps.InfoWindow({
                    map: toRaw(this.map),
                    content: '<div style="width:120px;text-align:center;padding:2px;"> : <b>'+ name +'</b></div>',
                    position: position,
                    removable: true,
                });                

            });
        },

        displayOlle() {

            let features = this.olle_data.features;
            
            features.forEach( element => {                
                let coord = element.geometry.coordinates;
                coord.forEach( points => {
                    var addpath=[];
                    points.forEach ( point => {
                        let position = new kakao.maps.LatLng(point[1], point[0]);
                        addpath.push(position);
                    });
                    new kakao.maps.Polyline({
                        map: toRaw(this.map),
                        path: addpath, //    
                        strokeWeight: 3, //   
                        strokeColor: '#0000FF', //  
                        strokeOpacity: 0.7, //    1 0   0  
                        strokeStyle: 'solid' //  
                    });
                });
            });
        },

        displayHalla() {
            
            let features = this.halla_data.features;
            
            features.forEach( element => {                
                let coord = element.geometry.coordinates;
                coord.forEach( points => {
                    var addpath=[];
                    points.forEach ( point => {
                        let position = new kakao.maps.LatLng(point[1], point[0]);
                        addpath.push(position);
                    });
                    new kakao.maps.Polyline({
                        map: toRaw(this.map),
                        path: addpath, //    
                        strokeWeight: 3, //   
                        strokeColor: '#FF0000', //  
                        strokeOpacity: 0.5, //    1 0   0  
                        strokeStyle: 'solid' //  
                    });
                });
            });

        },

        displayHiking() {

            let features = this.hiking_data.features;

            features.forEach( element => {                
                let coord = element.geometry.coordinates;
                coord.forEach( points => {
                    var addpath=[];
                    points.forEach ( point => {
                        let position = new kakao.maps.LatLng(point[1], point[0]);
                        addpath.push(position);
                    });
                    new kakao.maps.Polyline({
                        map: toRaw(this.map),
                        path: addpath, //    
                        strokeWeight: 2, //   
                        strokeColor: '#0000FF', //  
                        strokeOpacity: 0.5, //    1 0   0  
                        strokeStyle: 'solid' //  
                    });
                });
            });


        },
        
    },
};
</script>

